/* Import Google Fonts */
@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap");

/* Reset styles */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Titillium Web", sans-serif;
}

/* Color variables */
:root {
  --mainColor: #29335c;
  --mainColorLight: #5767aa;
  --secondaryColor: #db2b39;
  --textColor: #eee;
  --fadeColor: #ccc; /* Faded color */
}

/* Body styles */
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
}

/* Header styles */
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  padding: 0 2rem;
  background-color: var(--mainColor);
  color: var(--textColor);
  z-index: 1003; /* Ensure header is on top of other content */
}

/* Additional styles for circular profile picture in header */
.header-content {
  display: flex;
  justify-content: space-between; /* Distribute items on the main axis */
  align-items: center; /* Center items vertically */
  position: relative;
}

.profile-pic-container {
  margin-right: 10px; /* Adjust the margin as needed for spacing */
}

.profile-pic {
  width: auto; /* Adjust the size as needed */
  height: 150px; /* Set the height */
  max-width: 100%; /* Ensure the image doesn't exceed the container width */
  border-radius: 50%;
  transition: transform 0.3s ease-in-out;
}

.profile-pic:hover {
  transform: scale(1.1);
}

/* Navigation styles */
nav a {
  margin: 0 1rem;
  color: var(--textColor);
  text-decoration: none;
}

nav a:hover {
  color: var(--secondaryColor);
}

header .nav-btn {
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  color: var(--textColor);
  visibility: hidden;
  opacity: 0;
  font-size: 1.8rem;
  z-index: 1002; /* Ensure button is on top of other content */
}

header div,
nav {
  display: flex;
  align-items: center;
}

/* Main content styles */
main {
  flex: 1;
  padding: 2rem;
  background-color: #f4f4f4;
  z-index: 1000; /* Ensure main content is behind other elements */
}

/* Footer styles */
footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: var(--mainColor);
  color: var(--textColor);
  padding: 1rem;
  text-align: center;
  z-index: 1003; /* Ensure footer is on top of other content */
}

/* Responsive styles */
@media only screen and (max-width: 1024px) {
  header .nav-btn {
    visibility: visible;
    opacity: 1;
  }

  header nav {
    position: fixed;
    top: -100vh;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    background-color: var(--mainColor);
    transition: 1s;
    z-index: 1004; /* Ensure navigation is on top of other content */
  }

  header .responsive_nav {
    transform: translateY(100vh);
  }

  nav .nav-close-btn {
    position: absolute;
    top: 2rem;
    right: 2rem;
  }

  nav a {
    font-size: 1.5rem;
  }
}

/* Exclude main section from triggering horizontal scroll */
main section {
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

/* Apply pop-out effect to all sections except the main section */
main section:not(#afterSections) {
  margin-top: 0.5rem;
  transition: transform 0.3s ease-in-out;
  padding: 3rem;
  box-sizing: border-box;
}

main section:not(#afterSections):hover {
  transform: scale(1.05);
}

/* Style for faded color in the education section */
main section#education li::after {
  content: " -";
  color: var(--fadeColor);
}

/* After sections styles */
#afterSections {
  white-space: nowrap;
  overflow-x: auto; /* Allow horizontal scrolling */
  display: flex;
  flex-direction: row; /* Allow horizontal scrolling */
  align-items: center;
  padding: 2rem;
  margin-top: 1rem;
  background-color: #f8f9fa; /* Light background color */
  border: 2px solid #dee2e6; /* Light border color */
  border-radius: 10px; /* Rounded corners */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

#afterSections img {
  object-fit: cover; /* Maintain aspect ratio while covering the container */
  height: 500px; /* Set the height */
  width: 350px; /* Set the width */
  margin-right: 1rem; /* Add some margin between images for larger screens */
  border-radius: 50%; /* Make the images circular */
  transition: transform 0.3s ease-in-out;
}

#afterSections img:hover {
  transform: scale(1.1); /* Apply scale effect on hover */
}

/* Projects button styles */
.projects-button {
  padding: 10px 20px; /* Adjust padding as needed */
  font-size: 16px; /* Adjust font size as needed */
  border: none;
  border-radius: 25px; /* Make it rounder */
  background-color: var(--mainColor); /* Use the same color as your header */
  color: var(--textColor);
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s; /* Add a smooth transition */
}

.projects-button:hover {
  background-color: var(--mainColorLight); /* Lighten the color on hover */
  color: #fff; /* Change text color on hover */
}

/* Navigation overlay styles */
.navbar-dropdown-content {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8); /* Semi-transparent black background */
  z-index: 1001; /* Ensure the navigation is on top of other content */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

/* Style the links in the navigation overlay */
.navbar-dropdown-content a {
  margin: 1rem;
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
  transition: color 0.3s;
  z-index: 1002; /* Ensure the links are on top of the overlay background */
}

.navbar-dropdown-content a:hover {
  color: var(--secondaryColor);
}

.experience-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.experience-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1000px; /* Adjust the maximum width as needed */
  width: 100%;
  margin: 0 auto;
}

.experience-item {
  margin-bottom: 2rem;
  list-style: none; /* Remove the default list-style (bullet points) */
  padding: 0; /* Remove the default padding */
  margin: 0; /* Remove the default margin */
}

.education .experience-block,
.work-experience .experience-block {
  background-color: var(--mainColor);
  color: white;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
  margin-bottom: 2rem; /* Add margin-bottom to create space between blocks */
  width: 300px; /* Adjust the width as needed */
}

.experience-block:hover {
  transform: scale(1.05);
}

.vertical-line {
  width: 2px;
  height: 100%;
  background-color: var(--fadeColor);
  margin: 0 2rem; /* Adjust the margin as needed */
}

/* Projects component styles */
.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 2rem;
  margin-top: 2rem;
}

.project-card {
  width: 300px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease-in-out;
}

.project-card:hover {
  transform: scale(1.05);
}

.project-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.project-info {
  padding: 1.5rem;
}

.project-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.project-description {
  font-size: 1rem;
  color: #555;
  margin-bottom: 1rem;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.project-tag {
  background-color: var(--mainColor);
  color: #fff;
  font-size: 0.8rem;
  padding: 0.2rem 0.5rem;
  border-radius: 4px;
}

/* Media query for tablet screens */
@media only screen and (min-width: 768px) {
  .project-card {
    width: 350px;
  }
}

/* Media query for desktop screens */
@media only screen and (min-width: 1024px) {
  .project-card {
    width: 400px;
  }
}

/* Additional styles for the contact page */
.contact-container {
  text-align: center;
  margin-top: 2rem;
}

.logo-container {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 1rem;
}

.logo-wrapper {
  background-color: var(--mainColor);
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
}

.contact-logo {
  width: 50px; /* Adjust the width as needed */
  height: auto; /* Maintain aspect ratio */
  max-width: 100%; /* Ensure the image doesn't exceed the container width */
  border-radius: 8px; /* Add a border-radius for rounded corners */
  transition: transform 0.3s ease-in-out;
}

.contact-logo:hover {
  transform: scale(1.1); /* Apply the scale effect on hover */
}

/* Hide buttons underneath project images */
#afterSections .project-buttons {
  display: none;
}
